section.packages {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    padding: 15px 5%;
    letter-spacing: -.05em;
    position: relative;
}
section.packages>ul {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    padding: 0;
    
}
li.package-item {
    width: 350px;
    height: 500px;
    list-style: none;
    margin-bottom: 70px;
    padding: 3%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;
}

li.package-item>.header {
    display: flex;
    flex-direction: row;
    width: 100%;
    justify-content: space-between;
    align-items: center;
    padding: 0;
}
li.package-item>.numbers {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}
li.package-item>.days {
    height: 79px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
}
.bg-gradient {
    background: linear-gradient(111.49deg,hsla(0,0%,100%,.08) -8.95%,hsla(0,0%,100%,.002) 114%);
    border-radius: 4.89028px;
}
li.package-item>.amount {
    height: 45px;
    border-bottom: 1px solid #535b6d;
    width: 100%;
}
li.package-item>.amount>input {
    background: transparent;
    color: #fff;
    font-size: 16px;
    border-width: 0;
    outline: none;
    height: 75%;
    width: 100%;
    padding-left: 5px;
}
li.package-item>.amount>input::placeholder{
    color: rgba(250, 20, 20, 0.7)
}
li.package-item>.days-roi {
    width: 100%;
}
span.sm {
    font-weight: 400;
    font-size: 13px;
}
li.package-item>.days-roi>div {
    width: 100%;
    padding: 10px;
    margin-top: 10px;
}
li.package-item>button {
    height: 63px;
    width: 100%;
    font-size: 20px;
}

.bg-shadow {
    align-items: center;
    background: rgba(0,0,0,.3);
    border: 3px solid hsla(0,0%,100%,.3);
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    height: 100%;
    padding: 35px 50px;
    width: 100%;
}
section.packages>ul {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    padding: 0;
}
li.package-item>.header>.type {
    color: #f8d239;
}
li.package-item:first-child>.header>.type, li.package-item:nth-child(2)>.header>.type, li.package-item:nth-child(3)>.header>.type {
    color: #fff;
}
@media screen and (max-width: 750px){
    section.packages>ul {
        justify-content: center;
    }}