section.status>.status-box {
    display: flex;
    flex-direction: column;
    padding: 25px;
    justify-content: center;
    align-items: flex-start;
    background: rgba(0,0,0,.1);
    border-radius: 10px;
}
section.status>.status-box>.headline {
    align-self: center;
    font-size: 24px;
}
section.status>.status-box>div {
    font-size: 20px;
    margin-top: 6px;
}
section.status>.status-box>div {
    font-size: 20px;
