}
section.status>.status-box>.status-container {
    display: flex;
    flex-direction: column;
}
section.status>.status-box>.status-container>.status-item {
    display: flex;
    justify-content: flex-start;
    flex-wrap: nowrap;
    align-items: center;
}
section.status>.status-box>.status-container>.status-item>.icon {
    width: 60px;
    height: 60px;
    margin-right: 10px;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: 50%;
    background-image: url(/public/images/icono-status1.svg);
    height: 43px;
}
section.status>.status-box>.status-container>.status-item>.numbers>span.reg {
    font-size: 32px;
    font-weight: 700;
    color: rgba(232,65,66,.9);
    white-space: nowrap;
}
section.status>.status-box>.status-container>.status-item>.numbers>div {
    font-size: 14px;
    font-weight: 700;
}
section.status>.status-box>.status-container>.status-item:nth-child(2)>.icon {
    background-image: url(/public/images/icono-item-2.svg);
    height: 40px;
}
section.status>.status-box>.status-container>.status-item:nth-child(3)>.icon {
    background-image: url(/public/images/icono-status3.svg);
    height: 30px;
}
@media screen and (max-width: 750px){
section.status {
    flex-direction: column;
    justify-content: flex-start;
    padding: 30px 5%;
}}
@media screen and (max-width: 750px){
section.status>.promo {
    
    padding-right: 0;
    margin-bottom: 35px;
    background-image: url(/public/images/background-image.svg);
    background-size: contain;
    background-position: 50%;
    width: auto;
    font-size: 20px;
}}
@media screen and (max-width: 750px){
section.status>.promo>div {
    width: auto;
    font-size: 15px;
}}
