@media (min-width: 992px){
  section {
      max-width: 960px;
  }}
  @media (min-width: 768px){
  section {
      max-width: 720px;
  }}
  @media (min-width: 576px){
  section {
      max-width: 540px;
  }}
  header, section {
    max-width: 1320px;
    margin-right: auto!important;
    margin-left: auto!important;
    padding: 15px 5%;
}