header, section {
    max-width: 1320px;
    margin-right: auto!important;
    margin-left: auto!important;
    padding: 15px 5%;
}

header>.mid-head {
    justify-content: space-between;
    padding: 15px 5%;
    height: 100px;
}
header {
    display: block;
}
.banner,.mid-head {
    width: 100%;
    display: flex;
    align-items: center;
}
.banner {
    height: 40px;
    justify-content: flex-start;
    flex-direction: row;
}
* {
    box-sizing: border-box;
    font-family: "IBM Plex Sans",sans-serif;
}

.banner,.mid-head {
    width: 100%;
    display: flex;
    align-items: center;
}
.logo>img {
    width: 150px;
    height: 150px;
    margin-right: 20px;
}
nav.nav {
    width: 100%;
    padding: 0;
}
nav {
    display: block;
}
header>.mid-head>.right>button {
    padding: 10px 25px;
    font-size: 15px;
}
button.primary {
    background: rgba(232,65,66,.9);
}
button.btn {
    border-radius: 47px;
    border-width: 0;
    text-align: center;
    cursor: pointer;
    font-weight: 600;
    color: #000;
    transition: all .2s ease-in-out;
    outline: none;
}
button {
    appearance: auto;
    writing-mode: horizontal-tb !important;
    text-rendering: auto;
    color: -internal-light-dark(black, white);
    letter-spacing: normal;
    word-spacing: normal;
    line-height: normal;
    text-transform: none;
    text-indent: 0px;
    text-shadow: none;
    display: inline-block;
    text-align: center;
    align-items: flex-start;
    cursor: default;
    box-sizing: border-box;
    background-color: -internal-light-dark(rgb(239, 239, 239), rgb(59, 59, 59));
    margin: 0em;
    padding: 1px 6px;
    border-width: 2px;
    border-style: outset;
    border-color: -internal-light-dark(rgb(118, 118, 118), rgb(133, 133, 133));
    border-image: initial;
}
.head-element {
    margin-left: 5%;
    font-size: 14px;
}
.head-element>span {
    color: #6b6a63;
    font-weight: 400;
}

nav.nav>ul {
    width: 100%;
    height: 50px;
    list-style: none;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 0 5%;
    color: #fff;
    font-size: 20px;
    transition: all .2s ease-in-out;
}

@media screen and (max-width: 750px){
    header>.mid-head>.right>img {
        display: inline-block;
        cursor: pointer;
    }
}
header>.mid-head>.right>img {
    display: none;
}
@media screen and (max-width: 750px){
nav.nav {
    position: fixed;
    width: 100vw;
    height: calc(100vh - 140px);
    top: -100vh;
    left: 0;
    background: #1b1f29;
    z-index: 10;
    transition: all .2s ease-in-out;
}}
@media screen and (max-width: 750px){
header>.mid-head>.right>img {
    display: inline-block;
    cursor: pointer;
}}
@media screen and (max-width: 750px){
nav.nav.active {
    -webkit-transform: translateY(calc(100vh + 140px));
    transform: translateY(calc(100vh + 140px));
    left: 0;
}}

@media screen and (max-width: 750px){
nav.nav {
    position: fixed;
    width: 100vw;
    height: calc(100vh - 140px);
    top: -100vh;
    left: 0;
    background: #1b1f29;
    z-index: 10;
    transition: all .2s ease-in-out;
}}
@media screen and (max-width: 750px){
nav.nav>ul {
    height: 100%;
    flex-direction: column;
    justify-content: flex-start;
    color: #fff;
    gap: 1.2em
}
header>.mid-head>.right>img{
    margin-left: 10px
}
}
header>.mid-head>.logo {
    color: #6b6a63;
    font-weight: 700;
    font-size: 36px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    letter-spacing: .561983px;
}
.mid-head li{cursor: pointer}
.mid-head li:hover{color: rgba(232,65,66,.9);}

@media screen and (max-width: 750px){
header>.mid-head>.logo>img {
    width: 60px;
    height: 60px;
    margin-right: 6px;
}}
@media screen and (max-width: 750px){
section.status {
    flex-direction: column;
    justify-content: flex-start;
    padding: 30px 5%;
}}


@media screen and (max-width: 750px){
section.status>.status-box {
    width: 100%;
    margin-bottom: 25px;
}}