section.earnings {
    margin: 0 5%;
    padding-bottom: 20px;
    position: relative;
}
section.earnings>.list {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    margin-top: 25px;
   
}

section.earnings>.list>.staked {
    width: 370px;
    height: 310px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    padding: 2%;
    margin-right: 70px;
}
section.earnings>.list>.staked>div {
    width: 100%;
}

span.sm {
    font-weight: 400;
    font-size: 13px;
}
section.earnings>.list>.staked>button {
    height: 63px;
    width: 100%;
    font-size: 20px;
}
section.earnings>.list>.ref {
    height: 310px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    flex: 1 1;
    overflow: hidden;
    background-image: url(/public/images/background-div.svg);
    background-repeat: no-repeat;
    background-size: contain;
    background-position: 50%;
}
section.earnings>.list>.ref>.main {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    padding: 3%;
    flex: 1 1;
    width: 100%;
    font-weight: 700;
}
section.earnings>.list>.ref>.main>p {
    color: #fff;
    font-size: 12px;
}
section.earnings>.list div.bg-gradient {
    background: linear-gradient(111.49deg,hsla(0,0%,100%,.2) -8.95%,hsla(0,0%,100%,.02) 114%);
}
section.earnings>.list div.bg-gradient {
    width: 100%;
    padding: 10px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}
section.earnings>.list div.bg-gradient>img {
    cursor: pointer;
    width: 25px;
}
section.earnings>.list>.ref>.footer {
    width: 100%;
    height: 50px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: row;
    padding: 0 30px;
}
.head-element {
    margin-left: 5%;
    font-size: 14px;
}
section.earnings>.list>.ref>.footer>div>span {
    color: #fff;
}
@media screen and (max-width: 750px){
section.earnings>.list {
    flex-direction: column;
    width: 100%;
}}
@media screen and (max-width: 750px){
section.earnings>.list>.ref {
    min-height: 340px;
}}
@media screen and (max-width: 750px){
section.earnings>.list>.staked {
    width: 100%;
    height: 320px;
    margin-right: 0;
    margin-bottom: 25px;
}
section.earnings {
    margin: 0 5%;
    padding: 50px;
    position: relative;
}
}